
import { defineComponent, PropType } from 'vue'

interface bannerArr {
  isShow?: string,
  name: string,
  imgUrl: string
}

export default defineComponent({
  name: 'banner',
  props: {
    bannerList: {
      type: Array as PropType< bannerArr[] >,
      required: true
    }
  },
  components: {
  },
})
